.app {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #2a2634;
  color: white;
}

.header {
  width: 100%;
  height: 20px;
  display: flex;
  justify-content: left;
  align-items: center;
  background-color: #159D80;
  color: #2a2634;
  padding: 10px 0;
  text-decoration: none;
}

.title {
  margin: 0;
}

.logo {
  width: 20px;
  height: 20px;
  margin: 0 15px;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #159D80;
  color: #2a2634;
  height: 50px;
}

.view-every {
  cursor: pointer;
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.view-every:hover{
  background-color: rgb(127, 192, 178);
  border-radius: 10px;
}

.toggle-location {
  cursor: pointer;
  width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle-location:hover{
  background-color: rgb(127, 192, 178);
  border-radius: 10px;
}

.single-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

.every-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

.add-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

@media (min-width: 1024px) {
 .footer {
   height: 70px;
   margin-top: 10px;
   border-radius: 10px;
 }
};