
.locations {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 40px;
  height: 60%;
}

.locations-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  height: 70px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #159D80;
}

.row-city {
  width: 75%;
  height: 100%;
  padding: 0px 0px 0 20px;
  display: flex;
  justify-content: left;
  align-items: center;
  cursor: pointer;
}

.row-delete {
  width: 25%;
  height: 100%;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  cursor: pointer;
  color: #159D80;
}

.row-delete:hover {
  background-color: #159D80;
  color: white;
}

.row-city:hover {
  color: #159D80;
}

.view-add-big {
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.view-add-big:hover{
  background-color: rgb(127, 192, 178);
  border-radius: 10px;
}

@media (min-width: 1024px) {
  .every-main {
    width: 50%;
  }
};